.about{
    height: 475px;
    color: white;
    background-color: rgb(0, 0, 0);

}
.title{
    display: flex;
    justify-content: space-around;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.content{
    padding: 38px 230px 20px;
    font-size: 20px;

}
.content p {
    font-family: none;
    font-size: 1.3rem;
}
.icons{
    display: flex;
    justify-content: center;
    margin-top: 15px;
    padding: 10px;
    font-size: 2.1rem;
}
.icons svg{
    padding-right: 2rem;
    width:50px;
     height:50px;
}
.icons img{
    width: 50px;
    height: 50px;
    border-radius: 1rem;
    margin-right: 2rem;
}