.banner{
    background-image: url(./lap.jpg);
    background-position-x: center;
    flex-direction: column;
    object-fit:cover;
    background-size: cover;
    height: 630px;
    color: white;
}
@media screen and (max-width: 460px) {
    .banner{
        display: table-caption;     
    }
}
.fade{
    height: 630px;
    background: rgba(0, 0, 0, 0.8) 
}
